.divcard{
    height: 200px;
    background-color: #24272C;
    width: 350px;
    transition: 0.5s ease-in-out;
    animation-name: entrada;
    animation-duration: 0.8s;
}
@keyframes entrada{
    0%{
        width: 0px;
    }
    100%{
        width: 350px;
    }
}
.divcard:hover{
    scale: 1.05;
    transition: 0.5s ease-in-out;
}
.divimg{
    height: 60%;
    background-position-y: 50%;
    background-repeat: no-repeat;
    background-size: cover;
}
.desc{
    padding: 0px 4%;
}
.texto{
    color: #FCA311;
    font-weight: bolder;
}